body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Comfortaa', cursive;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.app-header{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80px;
  padding-right: 20px;
  padding-left: 10px;
  border-bottom-right-radius: 40px;
  background: #7F5541;
  position: fixed;
}

.app-header:hover{
  -webkit-transition-duration: .5s;
          transition-duration: .5s;
}

.inputArea{
  position: fixed;
}

.cards{
  background: #ffffff;
  display: flex;
  justify-content: center;
	flex-wrap: wrap;
}
.search-box{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.search-field{
    background: #CC5F2B;
    height: 40px;
    border-radius: 40px;
    padding: 4px;
}

.search-btn{
    color: #fff;
    float: right;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: #CC5F2B;
    display: flex;
    justify-content: center;
    align-items: center;
}

.search-field:hover > .search-text{
    width: 240px;
    padding: 0 6px;
}

.search-field:hover > .search-btn{
    background: #fff;
    color: #CC5F2B;
}

.search-field:focus{
    width: 240px;
}

.search-text{
    border: none;
    background: none;
    outline: none;
    padding: 0;
    color: #fff;
    font-size: 14pt;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    line-height: 40px;
    width: 0px;
    font-family: 'Comfortaa', cursive;
}
.visit-card{
    height: 320px;
    margin-right: 20px;
    margin-top: 10px;
    display: flex;
}

/* #region LEFT CONTENT AREA */
.visit-card .left-content{
    background: #FFD9D8;
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding-top: 32px;
}

.left-content .thumbnail img{
    height: 130px;
    border-radius: 100%;
    margin: 8px;
    border: 5px solid #f07033;
}

.left-content > .name-surname-job > .name-surname {
    font-size: 22px;
    color: #f07033;
}

.left-content > .name-surname-job > .job {
    font-size: 14px;
}

hr.bar-left {
border: 0;
height: 2px;
background: #f07033;
margin-left: 64px;
margin-right: 64px;
}

/* #endregion LEFT CONTET AREA */

/* #region RIGHT CONTENT AREA */
.visit-card > .right-content {
    background: #f07033;
    width: 60px;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 22px;

}

table {
    border-collapse: collapse;
    text-align: center;
    vertical-align: middle;
    margin: -10px;
}

.icon-info {
    color: #f07033;
    font-size: 22pt;
}

.right-content td.td-icon {
    background-color: white;
    border: 3px solid #f07033;
    align-items: center;
    padding: 4px;
    size: 24px;
}

.td-text {
    align-self: flex-start;
    text-align: start;
    font-size: 0;
    padding-left: 8px;
    padding-right: 8px;
}

td.td-endereco {
    background-color: #f07033;
}

div.text-endereco {
    color: white;
    -webkit-text-size-adjust: 8px;
        -ms-text-size-adjust: 8px;
            text-size-adjust: 8px;
}


/* ANIMATIONS AND TRANSITIONS*/
.right-content:hover {
    width: 260px;
    -webkit-transition: .5s;
    transition: .5s;
    padding-left: 10px;
    margin: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.right-content:hover table{
    margin: 0px;
}

.right-content:hover .icon-info{
    font-size: 12px;
    -webkit-transition: .5s;
    transition: .5s;
}

.right-content:hover .td-text {
    background-color: white;
    border: 3px solid #f07033;
    font-size: 12px;
    font-weight: bold;
    -webkit-transition: .5s;
    transition: .5s;
}


.name-surname{
    font-family: 'Raleway', sans-serif;
}

.job{
    font-family: 'Lato', sans-serif;
    font-weight: bold;
    font-size: 36pt;
    text-transform: uppercase;
}

td .text-info{
    font-family: 'Lato', sans-serif;
}
